* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-form {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin: auto;
}
.search {
  background-color: #269ce9;
  border-color: #269ce9;
  border-radius: 5px;
  width: 70px;
  height: 30px;
  color: #ffffff;
}

.search:hover {
  background-color: #70b9e8;
}

input[name='searchQuery'] {
  width: 220px;
  height: 30px;
}

.load-more {
  /* display: none; */
  margin: auto;
  padding: 7px;
  display: flex;
  background-color: #269ce9;
  border-color: #269ce9;
  border-radius: 5px;
  width: auto;
  text-align: center;
  color: #ffffff;
}
.load-more:hover {
  background-color: #70b9e8;
}
